.news-carousel {
  height: 4.66666667rem !important;
  overflow: hidden;
}

.news-carousel .slider-frame {
  height: inherit !important;
}

.news-carousel .slider-frame .slider-list {
  height: inherit !important;
}

.news-carousel .slider-frame .slider-list .slider-slide {
  height: inherit;
}

.carousel-item {
  height: 4.66666667rem !important;
  width: 100%;
  position: relative;
}

.carousel-item .carousel-image {
  height: 100%;
  width: 100%;
}

.carousel-item .carousel-title {
  position: absolute;
  top: 60%;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  color: #eee;
  opacity: 0.8;
  font-size: 0.29333333rem;
  width: 80%;
}


