.menu-container {
  -ms-flex: 1 1;
  flex: 1 1;
  width: 80%;
  overflow: hidden;
}

.menu-container .menu-content {
  white-space: nowrap;
}

.menu-item {
  display: inline-block;
  color: #C01010;
  background-color: #f5f5f5;
  font-size: 0.26666667rem;
  width: 1.6rem;
  text-align: center;
  border-radius: 0.06666667rem 0.06666667rem 0 0;
  padding: 0.06666667rem 0;
}

.menu-item.item-active {
  color: #eee;
  background-color: #C01010;
}


